<template>
  <div class="changer">
    <div class="container changer__wrap">
      <h3 class="all__notes" v-if="!search">
        {{ notes.length > 0 ? words.infobar[lang] : words.noinfobar[lang] }}
      </h3>
      <!-- <h3 class="all__notes" v-else>Поиск</h3> -->
      <button @click="chang" class="ch__btn">
        <div class="ch__cont" v-if="change">
          <img src="@/assets/img/flex.svg" alt="" />
          <span>{{ words.list[lang] }}</span>
        </div>
        <div class="ch__cont" v-else>
          <img src="@/assets/img/grid.svg" alt="" />
          <span>{{ words.grid[lang] }}</span>
        </div>
      </button>
    </div>
    <div class="container">
      <div :class="[change ? grid : flex]">
        <Note
          v-for="(note, index) in notes"
          :key="index"
          :note="note"
          @changeNote="$emit('changeNote', note.id)"
          @delNote="$emit('delNote', note.id)"
          :search="search"
          :lang="lang"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Note from "@/components/NoteItem.vue";
export default {
  name: "NotesTodo",
  components: {
    Note,
  },
  props: {
    notes: {
      typeof: Array,
    },
    search: String,
    lang: String,
  },
  inject: ["words"],
  data() {
    return {
      change: true,
      flex: "changer__flex",
      grid: "changer__grid",
    };
  },
  methods: {
    chang() {
      this.change = !this.change;
    },
  },
};
</script>

<style></style>