<template>
  <Transition name="scale">
    <div class="modal" @click="closeModal">
      <div class="modal__content" @click.stop="">
        <h3 class="modal__h3">
          {{ !edit ? words.titlewindow[lang] : words.titlewindowedit[lang] }}
        </h3>
        <div class="modal__inputs">
          <div class="input__content">
            <span class="input__label">Заголовок</span>
            <input
              type="text"
              class="modal__input"
              placeholder="Заголовок"
              v-model="title"
            />
          </div>
          <div class="input__content">
            <span class="input__label">Текст</span>
            <input
              type="text"
              class="modal__input"
              placeholder="Текст"
              v-model="desc"
            />
          </div>
        </div>
        <div class="modal__btns">
          <button class="modal__btn delete" @click="closeModal">
            {{ words.closebtn[lang] }}
          </button>
          <button class="modal__btn redact" v-if="!edit" @click="addNote">
            {{ words.addbtn[lang] }}
          </button>
          <button class="modal__btn redact" v-else @click="editedNote">
            {{ words.editwindowbtn[lang] }}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
export default {
  name: "ModalTodo",
  inject: ["words"],
  props: {
    edit: Boolean,
    currentId: Number,
    editNote: Object,
    lang: String,
  },

  data() {
    return {
      title: "",
      desc: "",
      id: this.currentId,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
      this.desc = "";
      this.title = "";
    },
    addNote() {
      if (this.desc != "" && this.title != "") {
        const item = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          desc: this.desc,
          id: this.id++,
        };
        this.$emit("addNote", item);
        this.desc = "";
        this.title = "";
      }
    },
    editedNote() {
      if (this.desc != "" && this.title != "") {
        const changeNote = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          desc: this.desc,
          id: this.editNote.id,
        };
        this.$emit("editedNote", changeNote);
        this.closeModal();
        this.desc = this.title = "";
      }
    },
  },
};
</script>

