<template>
  <header class="header">
    <div class="header__wrap">
      <button class="header__lang" @click="changeLang" v-if="lang == 'ru'">UZ</button>
      <button class="header__lang" @click="changeLang" v-else>RU</button>
      <div class="container">
        <div class="header__content">
          <Transition name="opp">
            <p class="header__p" v-if="header">{{ words.appbartitle[lang] }}</p>
            <input type="text" class="header__input" :placeholder="words.appbarsearch[lang]" v-else v-model="search" />
          </Transition>
        </div>
      </div>
      <button class="header__search" @click="changeHeader">
        <img src="@/assets/img/search.svg" v-if="header" alt="" />
        <img src="@/assets/img/close.svg" alt="" v-else />
      </button>
    </div>
  </header>
</template>

<script>
export default {
  name: "NavbarTodo",
  props: {
    lang: String
  },
  inject: ['words'],
  data() {
    return {
      // lang: "ru",
      header: true,
      search: "",
    };
  },
  methods: {
    changeLang() {
      this.$emit('changeLang', this.lang == 'ru' ? 'uz' : 'ru')
    },
    changeHeader() {
      this.header = !this.header;
      this.search = "";
    },
  },
  watch: {
    search(val) {
      this.$emit("getSearch", val);
    },
  },
};
</script>
