<template>
  <Navbar @getSearch="search = $event" :lang="lang" @changeLang="changeLang" />
  <Notes
    :notes="filterNotes"
    :search="search"
    @changeNote="changeNote"
    @delNote="delNote"
    :lang="lang"
  />
  <AddBtn @openModal="openModal" />
  <Modal
    v-show="modalOpen"
    @closeModal="closeModal"
    :edit="edit"
    :currentId="currentId"
    @addNote="addNote"
    :editNote="editNote"
    @editedNote="editedNote"
    :lang="lang"
  />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddBtn from "@/components/Addbtn.vue";
import Modal from "@/components/Modal.vue";
import langs from "@/lang.js";
export default {
  name: "Todo",
  components: {
    Navbar,
    Notes,
    AddBtn,
    Modal,
  },
  data() {
    return {
      notes: [],
      modalOpen: false,
      edit: false,
      currentId: 1,
      editNote: {},
      search: "",
      lang: "ru",
      langwords: {},
    };
  },
  computed: {
    filterNotes() {
      return this.search
        ? this.notes.filter((item) =>
            item.title.toLowerCase().includes(this.search.toLowerCase())
          )
        : this.notes;
    },
  },
  methods: {
    openModal() {
      this.modalOpen = true;
      this.edit = false;
    },
    closeModal(status) {
      this.edit = this.modalOpen = status;
    },
    addNote(item) {
      if (item.title != "" && item.desc != "") {
        this.notes.push(item);
        this.modalOpen = false;
      }
    },
    changeNote(id) {
      this.edit = this.modalOpen = true;
      let pickedNote = this.notes.find((note) => note.id == id);
      this.editNote = pickedNote;
    },
    editedNote(noteEdited) {
      this.notes.forEach((note) => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      });
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes() {
      const localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    changeLang(val) {
      this.lang = localStorage.lang = val;
    },
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
  created() {
    this.getNotes();
    localStorage.lang = localStorage.lang || "ru";
    this.lang = localStorage.lang || "ru";
    this.langwords = langs;
    localStorage.words = JSON.stringify(this.langwords);
  },
  provide() {
    return {
      words: localStorage.words
        ? JSON.parse(localStorage.words)
        : this.langwords,
    };
  },
};
</script>
